export default {
	data() {
		return {
			searchValue : '', //搜索框值
			sixPalaceList : [], //六宫格列表
			screenIndex : 0, //筛选框默认值
			priceOrder : 0, //1 价格从低到高  2价格从高到低
			cateMaskState: 0, //分类面板展开状态
			scenicList : [], //景区列表
			loadingType: 'more', //加载更多状态 
			cateId: 0, //已选三级分类id
			cateList: [],//分类数组
		}
	},
	onLoad(options) {
		this.cateId = options.tid;
		this.loadCateList(options.fid,options.sid);
		this.lyfwData();
		this.loadData();
	}, 
	
	methods: {
		//请求模拟接口数据
		async lyfwData(){
			let sixPalaceList = await this.$api.lyfwfmq('sixPalaceList');
			this.sixPalaceList = sixPalaceList;
			// console.log(this.sixPalaceList)
		},
		
		//加载分类
		async loadCateList(fid, sid){
			let list = await this.$api.sceniclist('cateList');
			let cateList = list.filter(item=>item.pid == fid);
			cateList.forEach(item=>{
				let tempList = list.filter(val=>val.pid == item.id);
				item.child = tempList;
			})
			this.cateList = cateList;
		},
		
		//搜索框-搜索
		searchNow : function(){
			if(this.searchValue ==''){
				uni.showToast({
					title:'未输入搜索关键字',
					icon:'none',
					duration:1000
				});
				// retuan false;
			}
			uni.showToast({
				title:'你搜索了'+this.searchValue,
				icon:'none',
			})
			this.searchValue = ''
		},
		
		//路由整合
		godetail : function (value){
			uni.showToast({
				title: '点击了'+value,
				icon: 'none',
			});
			setTimeout(function(){
				uni.navigateTo({
					url : '/pages/LYFW/scenicSpotTickets/ticketsDetails'
				})
			},500);
		},
		
		//筛选点击
		tabClick(index){
			if(this.screenIndex === index && index !== 2){
				return;
			}
			this.screenIndex = index;
			if(index === 2){
				this.priceOrder = this.priceOrder === 1 ? 2: 1;
			}else{
				this.priceOrder = 0;
			}
			uni.pageScrollTo({
				duration: 300,
				scrollTop: 0
			})
			this.loadData('refresh', 1);
		},
		
		//显示分类面板
		toggleCateMask(type){
			let timer = type === 'show' ? 10 : 300;
			let	state = type === 'show' ? 1 : 0;
			this.cateMaskState = 2;
			setTimeout(()=>{
				this.cateMaskState = state;
			}, timer)
		},
		
		//分类点击
		changeCate : function(item){
			// console.log(item)
			this.cateId = item.id;
			this.toggleCateMask();
			uni.pageScrollTo({
				duration: 300,
				scrollTop: 0
			})
			this.loadData('refresh', 1);
			uni.showLoading({
				title: '正在搜索'+item.name,
			})
		},
		
		
		//加载信息 ，带下拉刷新和上滑加载
		async loadData(type='add', loading) {
			//没有更多直接返回
			if(type === 'add'){
				if(this.loadingType === 'nomore'){
					return;
				}
				this.loadingType = 'loading';
			}else{
				this.loadingType = 'more'
			}
			
			let scenicList = await this.$api.lyfwfmq('scenicList');
			if(type === 'refresh'){
				this.scenicList = [];
			}
			
			//筛选，测试数据直接前端筛选了
			if(this.screenIndex === 0){
				scenicList.sort((a,b)=>a.scenicId - b.scenicId)
			}
			if(this.screenIndex === 1){
				scenicList.sort((a,b)=>b.sales - a.sales)
			}
			if(this.screenIndex === 2){
				scenicList.sort((a,b)=>{
					if(this.priceOrder == 1){
						return a.adultPrice - b.adultPrice;
					}
					return b.adultPrice - a.adultPrice;
				})
			}
			
			this.scenicList = this.scenicList.concat(scenicList); 
			
			// 判断是否还有下一页，有是more  没有是nomore(测试数据判断大于20就没有了)
			// this.loadingType  = this.scenicList.length > 20 ? 'nomore' : 'more';
			if(type === 'refresh'){
				if(loading == 1){
					uni.hideLoading()
				}else{
					uni.stopPullDownRefresh();
				}
			}
		}
		
		
		
	}
}
